
@mixin clearfix() {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin no-outline() {
  outline: none;
  background: transparent;
  &::before {
    display: none;
  }
}

@mixin wrap() {
  &__wrap {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  @media screen and(max-width: 992px) {
    &__wrap {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
}

@mixin grid() {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: -10px;
  @media screen and (max-width: 992px){
    margin: 10px;
  }
}

@mixin grid-mix() {
  margin: 10px;
  &--product {
    width: calc((100% - 20px) / 6.66);
    min-width: 178px;
    text-align: left;
  }
  &--info {
    width: calc((100% - 20px) / 3.3);
    text-align: left;
  }
  &--quantity {
    width: calc((100% - 20px) / 14.44);
  }
  &--price {
    width: calc((100% - 20px) / 3.1);
  }
  &--del {
    width: calc((100% - 20px) / 14.44);
  }
  @media screen and (max-width: 992px){
    max-width: 100%;
    box-sizing: border-box;
  }
}

$orange: #ff6c00;